// layout mixins - start

@mixin mkdfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin mkdfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin mkdfFlexBoxLayout($position: null, $align-items: null, $justify-content: null) {
    @if ($position) {
        position: $position;
    }
    
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    
    @if ($align-items) {
        $older-align-items: $align-items;
        
        @if ($align-items == 'flex-start') {
            $older-align-items: start;
        } @else if ($align-items == 'flex-end') {
            $older-align-items: end;
        }
        
        -webkit-box-align: $older-align-items;
        -webkit-align-items: $align-items;
        -ms-flex-align: $older-align-items;
        align-items: $align-items;
    }
    
    @if ($justify-content) {
        $older-justify-content: $justify-content;
        
        @if ($justify-content == 'flex-start') {
            $older-justify-content: start;
        } @else if ($justify-content == 'flex-end') {
            $older-justify-content: end;
        }
        
        -webkit-box-pack: $older-justify-content;
        -webkit-justify-content: $justify-content;
        -ms-flex-pack: $older-justify-content;
        justify-content: $justify-content;
    }
}

@mixin mkdfInlineFlexLayout() {
    display: -webkit-inline-flex;
    display: -ms-inline-flexbox;
    display: inline-flex;
}

@mixin mkdfFlexContainer($align-items: null, $justify-content: null, $flex-direction: null, $flex-wrap: null, $align-content: null) {
	@include mkdfFlexBoxLayout(null, $align-items, $justify-content);
	
	@if ($flex-direction) {
		flex-direction: $flex-direction;
	}
	
	@if ($flex-wrap) {
		flex-wrap: $flex-wrap;
	}
	
	@if ($align-content) {
		align-content: $align-content;
	}
}

@mixin mkdfFlexItem($order: 0, $flex-grow: 0, $flex-shrink: 1, $flex-basis: auto) {
    order: $order;
    flex-grow: $flex-grow;
    flex-shrink: $flex-shrink;
    flex-basis: $flex-basis;
}

@mixin mkdfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin mkdfTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin mkdfTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

// layout mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// transition mixins - start

@mixin mkdfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin mkdfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin mkdfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin mkdfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin mkdfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

// transition mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;

%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;

    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }

    input[type=checkbox] + label {
        position: absolute;
        top: 0;
        left: 0;
        display: inline-block;
        line-height: 0;
        pointer-events: none;
        cursor: pointer;
    }

    input[type=checkbox] + label span.mkdf-label-text {
        display: inline-block;
        padding-left: 10px;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }

    input[type=checkbox] + label .mkdf-label-view {
        display: inline-block;
        vertical-align: top;
        width: $checkbox-size;
        height: $checkbox-size;
        background-color: $default-background-color;
        border: $checkbox-border-width solid $default-border-color;
        border-radius: 2px;
        cursor: pointer;
        box-sizing: border-box;

        &:hover {
            cursor: pointer;
        }
    }

    input[type=checkbox] + label .mkdf-label-view:after {
        content: '';
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        background-color: $first-main-color;
        opacity: 0;
        @include mkdfTransition(opacity 0.3s ease-in-out);
    }

    input[type=checkbox]:checked + label .mkdf-label-view:after {
        opacity: 1;
    }
}

// checkbox mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// common mixins - start

@mixin mkdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include mkdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .2);
            opacity: 0;
            @include mkdfTransition(opacity .4s ease-in-out);
        }

    } @else {
        @include mkdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        @include mkdfTransition(opacity .4s ease-in-out);
    }
}

@mixin mkdfStandardPaginationStyle($list_type: null) {
    @include mkdfRelativeHolderLayout(top);
    margin: 0;
    clear: both;

    ul {
        @include mkdfRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: left;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 5px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0;
                font-size: 15px;
                color: $first-main-color;
                font-weight: 400;
            }

            &.mkdf-pag-active,
            &:hover {
                a {
                    color: $default-heading-color;
                }
            }

            &.mkdf-pag-prev,
            &.mkdf-pag-next,
            &.mkdf-pag-first,
            &.mkdf-pag-last {
                margin: 0 2px;
                color: $first-main-color;

                &:hover {
                    a{
                        color: $default-heading-color;
                    }
                }

                a {
                    font-size: 22px;
                    line-height: 26px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                
                &.mkdf-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.mkdf-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $default-text-color;
                    font-size: 15px;
                    font-weight: 300;
                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            font-size: 22px;
                            line-height: 26px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        &:before {
                            content: "\34";
                        }
                    }

                    &.next {
                        &:before {
                            content: "\35";
                        }
                    }
                }
            }
        }
    }
}

@mixin mkdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $additional-font;
    font-size: 12px;
    letter-spacing: 0.5em;
    line-height: 2.4em;
    font-weight: 400;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;
    @include mkdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin mkdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 1px 16px;
        font-size: 10px;

    } @else if ($size == 'medium') {
        padding: 1px 16px;

    } @else if ($size == 'large') {
        padding: 5px 36px;
        font-size: 14px;
        line-height: 2.36em;

    } @else if ($size == 'huge') {
        display: block;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin mkdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin mkdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 1px solid transparent $important;
}

@mixin mkdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin mkdfButtonOutlineColor($important: null) {
    color: $default-heading-color $important;
    background-color: transparent $important;
    border: 1px solid #959595 $important;
}

@mixin mkdfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin mkdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 $input-margin;
    padding: $input-vertical-padding $input-horizontal-padding;
    font-family: inherit;
    font-size: 14px;
    font-weight: inherit;
    line-height: calc(#{$input-height} - (#{$input-vertical-padding} * 2) - 2px);
    color: $default-heading-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    cursor: text;
    -webkit-appearance: none;
    box-sizing: border-box;
    @include mkdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include mkdfPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

